.modal {
  width: 100%;
  max-width: 380px;
  padding-top: 27px;
}

.caption {
  color: rgba(26, 26, 26, 0.5);
}

.title {
  padding: 0;
  text-align: center;
  margin: 0;
  & + * {
    margin-top: 24px;
  }
}
.text {
  text-align: center;
}

.textInput {
  height: 40px;
  & + .textArea {
    margin-top: 8px;
  }
}

.textArea {
  height: 212px;
}

.approval {
  margin-top: 16px;
}

.btn {
  margin-left: auto;
  margin-right: auto;
  background-color: #128eb9 !important;
}

.load {
  display: flex;
  justify-content: center;
  & + * {
    margin-top: 15px;
  }
}

.controls {
  position: absolute;
  display: flex;
  right: 16px;
  top: 27px;
  gap: 12px;
  .iconAspect {
    cursor: pointer;
    width: 28px;
    height: 28px;
    padding: 6px;
    box-shadow: 0px 2px 12px rgba(0, 0, 0, 0.08);
    border-radius: 4px;
    transition: all 0.3s;
    border: 0;
    background: transparent;
    background-color: #fff;
    transition: all 0.3s;
    &:hover {
      opacity: 0.7;
    }
  }
  .btnCloseSmall {
    position: relative;
    right: auto;
    top: auto;
    width: 28px;
    height: 28px;
    padding: 8px;
  }
}
