.modal {
  width: 100%;
  max-width: 300px;
  padding: 0;
}

.title {
  padding: 0;
  margin: 0;
  & + * {
    margin-top: 24px;
  }
}

.btn {
  margin: 32px auto 0;
  width: 100%;
}

.top {
  padding: 23px 16px;
  border-bottom: solid 1px rgba(196, 196, 196, 0.3);
}

.list {
  height: 366px;
  padding-right: 11px;
  overflow-x: hidden;
  overflow-y: auto;
  scrollbar-color: #fff #253861;
  &::-webkit-scrollbar {
    width: 8px;
    background-color: rgba(196, 196, 196, 0.3);
    border-radius: 16px 16px 16px 0px;
    right: -5px;
  }

  &::-webkit-scrollbar-thumb {
    background: #5629e1;
    border-radius: 55px;
  }

  &::-webkit-scrollbar-thumb:hover {
    background-color: #fff;
  }
  .item {
    display: flex;
    align-items: center;
    & + .item {
      margin-top: 8px;
    }
  }
}

.picture {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  &.active {
    border: 1px solid #00c48c;
  }
}
.text {
  flex: 1;
  padding-left: 12px;
  padding-right: 12px;
  .title {
    margin: 0;
    & + * {
      margin-top: 0;
    }
  }
  .desc {
    display: block;
    font-size: 12px;
    color: rgba(26, 26, 26, 0.5);
  }
}

.controls {
  display: flex;
  gap: 12px;
  align-items: center;
  .arrow {
    width: 25px;
    height: 25px;
    border: 0;
    cursor: pointer;
    background: white;
    box-shadow: 0px 2px 12px rgba(0, 0, 0, 0.04);
    border-radius: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    &:hover {
      box-shadow: 0px 2px 12px rgba(0, 0, 0, 0.08);
    }
    img {
    }
  }
}

.icon {
  display: flex;
  justify-content: flex-end;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  box-shadow: 0px 2px 12px rgba(0, 0, 0, 0.08);
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: opacity 0.3s;
  &:hover {
    opacity: 0.55;
  }
}
