.modal {
  width: 100%;
  max-width: 380px;
  padding-top: 40px;
}

.title {
  text-align: center;
  padding: 0;
  margin: 0;
  & + * {
    margin-top: 24px;
  }
}

.sort {
  & + .textInput {
    margin-top: 8px;
  }
}

.inputListWrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 100%;
  gap: 8px;
  margin-top: 8px;
  .inputWrapper {
    width: 100%;
    &:nth-child(2),
    &:nth-child(3) {
      width: calc(50% - 6px);
    }
  }
}

.btn {
  margin: 32px auto 0;
  width: 100%;
}

.textInput {
  height: 40px;
}
.hidden {
  display: none;
}
