.modal {
    width: 100%;
    max-width: 375px;
    padding-top: 32px;
}

.caption {
    color: rgba(26, 26, 26, 0.5);
    text-align: center;
    display: block;
}

.title {
    text-align: center;
    padding: 0;
    margin: 0;
    & + * {
        margin-top: 24px;
    }
}

.controls {
    margin-top: 24px;
    display: flex;
    justify-content: center;
    gap: 25px;
    .btn {
        min-width: 137px;
    }
}
