.feedBack {
    margin-top: 32px;
    background: #fff;
    box-shadow: 8px 4px 14px rgba(0, 0, 0, 0.04);
    border-radius: 16px;
    position: relative;
    padding: 25px 20px;

    .top {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        align-items: center;
        .title {
            margin: 0;
        }
        .count {
            font-weight: 500;
            font-size: 16px;

            color: #1a1a1a;
            .num {
                color: rgba(26, 26, 26, 0.5);
                margin-left: 6px;
            }
        }
    }
    .body {
        margin-top: 24px;
    }
}

.jobCompleted {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    gap: 8px 25px;
    padding: 32px 0;
    .tit {
        font-weight: 500;
        font-size: 16px;
        color: #000000;
    }
    .btn {
    }
}

.chats {
    background-color: #ffffff;
    border: 1px solid #f7f7f7;
    box-shadow: 8px 4px 14px rgba(0, 0, 0, 0.04);
    border-radius: 16px;
    padding: 24px 26px;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    flex-wrap: wrap;
    .message {
        width: 60%;
        align-items: flex-end;
    }
    .answer {
        align-self: flex-end;
        width: 60%;
    }
}

@media (max-width: 767px) {
    .feedBack {
        .top {
            .count {
                font-size: 12px;
            }
        }
    }

    .chats {
        padding: 12px 16px;
        .message {
            width: 100%;
        }
        .answer {
            width: 100%;
        }
    }
}
